<template>
<div >
<!--  table-tree样例-->
 <el-card shadow="always"  >
   <el-row style="height: 100%;">
     <el-col :span="4" >

       <el-tree
         style=" height: calc(100vh);width:100%;"

         v-if="showTree"
         :data="treeData"

         show-checkbox
         default-expand-all
         node-key="id"
         ref="tree"
         highlight-current
         :props=" {children: 'detail',label: 'caption' }"
         @node-click="handleTreeNodeClick"
         @check="handleCheck"
       >
       </el-tree>


     </el-col>
     <el-col  :span="1" class="slide-button" style="width:25px;" @click="showHiddenTree" >
       <i :class="icon1"
          style="height:100%;display: flex;
          flex-direction: row;
          justify-content: center;align-items: center;"
          @click="showHiddenTree"

       ></i>
     </el-col>
     <el-col  :span="19">
<!--       -->
       <div  style="float:right;padding-bottom:5px;">

         <el-button type="primary" @click="showMessage2">新 增</el-button>
         <el-button type="primary" @click="showMessage">授 权</el-button>
       </div>

<!--       @change="queryTable"-->
       <el-input
         v-model="searchTableData"
         placeholder="输入权限名称关键字进行搜索"/>

       <el-table
