         :tree-props="{children: 'detail', hasChildren: 'hasChildren'}">

               <el-table-column
                 type="selection"
                 :selectable="isSelect"

                 width="55">
               </el-table-column>

         <el-table-column

             prop="caption"
             label="权限名称"
             width="180">


           </el-table-column>
           <el-table-column

             prop="id"
             label="ID"
             width="180">
           </el-table-column>
         <el-table-column
             prop="vue"
             label="组件地址"
             width="180">
           </el-table-column>
         <el-table-column
             prop="url"
             label="权限地址"
             width="180">
           </el-table-column>
         <el-table-column
           prop="icon"
           label="图标"
           width="180">
         </el-table-column>
         <el-table-column
           label="操作"
           width="180">
             <template slot-scope="scope">
               <el-button
                 v-show="scope.row.id!=='00-0' && scope.row.id!=='00-1'"
