                 size="mini"
                 @click.native.stop="handleEditRow(scope.$index, scope.row)"></el-button>
               <el-button
                 v-show="scope.row.id!=='00-0' && scope.row.id!=='00-1'"
                 type="danger"  icon="el-icon-delete" circle
                 size="mini"
                 @click.native.stop="handleDeleteRow(scope.$index, scope.row)"></el-button>
             </template>
         </el-table-column>
       </el-table>

     </el-col>
   </el-row>
 </el-card>



  <!--dialog2-->
  <el-dialog :visible.sync="permissionFormVisible1"
             :close-on-press-escape="false"
             :close-on-click-modal="false"
             :show-close="false"

  >
    <div slot="title"  style="text-align: left;font-size:20px;font-weight: bold;">
      <span  >{{this.tableDialgTitle}}</span>
    </div>
    <!--form start-->
    <el-form label-position="left" :model="permissionForm" :rules="permissionFormRules" ref="permissionForm" label-width="130px"   style="text-align: left;font-size:20px;font-weight: bold;">

              <el-form-item  label="权限名称" prop="caption" required :rules="this.permissionFormRules.caption">
                <el-input v-model="permissionForm.caption"></el-input>
              </el-form-item>

              <el-form-item  label="组件地址" prop="vue" required :rules="this.permissionFormRules.vue">
                <el-input v-model="permissionForm.vue"></el-input>
              </el-form-item>



              <el-form-item label="权限地址" prop="url" required :rules="this.permissionFormRules.url">
                <el-input v-model="permissionForm.url"></el-input>
              </el-form-item>

              <el-form-item label="图标" prop="icon" required :rules="this.permissionFormRules.icon">
                <el-input v-model="permissionForm.icon"></el-input>
              </el-form-item>
              <el-form-item  v-show="true" label="flag" prop="flag" required :rules="this.permissionFormRules.flag">
