             <el-form-item v-show="true" label="ID" prop="id" required :rules="this.permissionFormRules.id">
                <el-input v-model="permissionForm.id"></el-input>
              </el-form-item>

    </el-form>
    <!--form end-->
    <div slot="footer" class="dialog-footer">
      <el-button @click="resetForm('permissionForm')">重 置</el-button>
      <el-button @click="permissionFormVisible1 = false">取 消</el-button>
      <el-button type="primary" @click="submitForm('permissionForm')">确 定</el-button>
    </div>
  </el-dialog>


</div>
</template>

<script>
export default {
  name: "MenuManagerForSa",
  data(){
    let validateCaption = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('权限名称不可为空'));
      } else {
        callback();
      }
    };
    let validateVue = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('VUE组件地址不可为空'));
      } else {
        callback();
      }
    };
    let validateUrl = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('权限地址不可为空'));
      } else {
        callback();
      }
    };
    let validateIcon = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('图标不可为空'));
      } else {
        callback();
      }
    };
    let validateId = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('唯一ID不可为空'));
      } else {
        callback();
      }
    };
    let validateFlag = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('约束不可为空'));
      } else {
        callback();
      }
    };
    return{

      icon1:"el-icon-d-arrow-left",
      showTree:true,
      treeCheckedKeys:[],
      treeData:[
        {id:'100',caption:'系统配置',detail:[
            {id:'100_1',caption:'短信配置'},
            {id:'100_2',caption:'微信配置'},
          ]},
        {id:'200',caption:'业务查询',detail:[
            {id:'200_1',caption:'实时数据查询'},
            {id:'200_2',caption:'历史数据查询',detail:[
                {id:'200_2_1',caption:'回水温度'},
                {id:'200_2_2',caption:'阀门开度',detail:[
                    {id:'200_2_2_1',caption:'调控单元阀'},
                    {id:'200_2_2_2',caption:'调控户端阀',detail:[
                        {id:'200_2_2_2_1',caption:'室内最高温度'},
                        {id:'200_2_2_2_2',caption:'室内平均温度',detail:[
                            {id:'200_2_2_2_2_1',caption:'每小时采集'},
                            {id:'200_2_2_2_2_2',caption:'每天采集',detail:[
                                {id:'200_2_2_2_2_2_1',caption:'11栋4单元'},
                                {id:'200_2_2_2_2_2_2',caption:'12栋1单元',detail:[
                                    {id:'200_2_2_2_2_2_2_1',caption:'102-05-02张先生'},
                                    {id:'200_2_2_2_2_2_2_2',caption:'509-01-03李先生'},
                                  ]},
                              ]},
                          ]},
                      ]},
                  ]},
              ]},
          ]},

      ],

      //tableData:this.customGlobal.userMenuForPermission,
      searchTableData: '',
      tableData:[],
      tableDataClone:[],


      tableCheckedKeys: false,
      multipleSelection: [],
      tableRowIdMap:new Map(),

      // currentSelTableRow:null,//记载当前选择行
      permissionType :'all',//heatconfig,platfromconfig,all
      refreshTableData:new Date().getTime(),
      tableDialgTitle:'',//新增，修改
      tableOperFlag:'',//1是新增，2是修改，3是删除。
      permissionFormVisible1:false,
      permissionForm: {
        caption: '',
        vue: '',
        url: '',
        icon: '',
        id:'',
        oldId:'',//保存tableTree当前选择行的id
        flag:'',//1是新增，2是修改，3是删除。
        layer:'',//1是同级，2是子级
      },
      permissionFormRules: {
        caption: [
          {validator: validateCaption, trigger:'blur'}
        ],
        vue: [
          {validator: validateVue, trigger:'blur'}
        ],
        url:  [
          {validator: validateUrl, trigger:'blur'}
        ],
        icon:  [
          {validator: validateIcon, trigger:'blur'}
        ],
        id: [
          {validator: validateId, trigger:'blur'}
        ],
        flag: [
          {validator: validateFlag, trigger:'blur'}
        ],
      }
    }

  },
  created() {
    this.initTableTreeData();

    //初始化渲染
    // this.$nextTick(() => {
    //   this.initLoopCheckTable(this.tableDataClone);
    // })
  },
 watch:{
   searchTableData(newVal,oldVal){
     if(newVal===''){
       this.tableDataClone = this.tableData;
     }else{

       this.checkPermissionIdSuper(newVal,this.tableDataClone,{},'1','4');
       this.refreshTableData = new Date().getTime();
     }
   }
 },

  methods:{
    // queryTable:function(){
    //   var search=this.searchTableData;
    //   this.checkPermissionIdSuper(search,this.tableDataClone,{},'1','4');
    //   this.refreshTableData = new Date().getTime();
    // },

    //将用户权限点赋予到tableData
    initTableDataPermissionStatus(permissionKey,tableData){
      //let permissionkey={"2":true,"3":true,"UUID_20012001_20012001_001":true,"UUID_10011001_10011001_002":true,"4":true,"UUID_4_1":true};
      for (let k of Object.keys(permissionKey)) {
        this.checkPermissionIdSuper(k,tableData,
          {},
          '0',
          '5');
      }
      //初始化渲染
      this.$nextTick(() => {
        // that.initLoopCheckTable(that.tableDataClone);
        this.initLoopCheckTable(tableData);
      })
    },

    postModifyPermisson(heatconfig_detail,platfromconfig_detail){
      let that = this;
      let _jsondata_ =   {
        config:this.permissionType,//platfromconfig,heatconfig,all
        heatconfig_detail:heatconfig_detail,
        platfromconfig_detail:platfromconfig_detail
      } ;
      const loading = this.$loading({
        lock: true,
        text: '查询中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, .7)'
      });
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postModifyPermissionTemplate,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
      );
      p1.then((res) => {
        console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success'){

          loading.close();
          this.$message({
            type: 'success',
            showClose: true,
            duration:3000,
            dangerouslyUseHTMLString: true,
            // iconClass:'el-icon-info',
            message: '<spand style="color:black;"> 保存成功 </spand>'
          });
        }else{
          console.info("保存失败");
          loading.close();

          this.$alert(res.data.msg, '保存失败', {
            confirmButtonText: '确定',
            callback: action => {
              console.info('data:'+JSON.stringify(res.data));
              if(res.data.msg==='请登录认证'){
                this.$router.push({path:'/login'});
              }
            }
          });
        }

      },error=>{
        console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();
        this.$alert(JSON.stringify(error.message), '保存出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();

        this.$alert(JSON.stringify(res.message), '保存出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    initTableTreeData(){
      let that = this;
      let _jsondata_ =   {
          config:this.permissionType//platfromconfig,heatconfig,all
      } ;
      const loading = this.$loading({
        lock: true,
        text: '查询中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, .7)'
      });
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postQueryPermissionTemplate,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
      );
      p1.then((res) => {
        console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success'){

          loading.close();
          let permission = JSON.parse( res.data.permission);
          if(this.permissionType==='all'){
            that.tableData = [
              {
                caption:'热力用户权限',
                vue: '',
                url: '',
                icon: '',
                id:'00-0',//特殊标识
                detail:permission.heatconfig.permissionconfig.detail
              },
              {
                caption:'平台用户权限',
                vue: '',
                url: '',
                icon: '',
                id:'00-1',//特殊标识
                detail:permission.platfromconfig.permissionconfig.detail
              }

            ];
          }else{
            that.tableData = permission.permissionconfig.detail;
          }

          ////
          //模拟用户权限点
          //  let permissionKey={"UUID_20012001":true,"2":true,"3":true,"UUID_20012001_20012001_001":true,"UUID_10011001_10011001_002":true,"UUID_10011001_10011001_003":true,"UUID_10011001_10011001_005":true,"UUID_10011001_10011001_006":true,"4":true,"UUID_4_1":true};
          let permissionKey={"2":true,"3":true,"UUID_20012001_20012001_001":true,"UUID_10011001_10011001_002":true,"4":true,"UUID_4_1":true};
          // for (let k of Object.keys(permissionkey)) {
          //   that.checkPermissionIdSuper(k,that.tableData,
          //     {},
          //     '0',
          //     '5');
          // }
          // //初始化渲染
          // that.$nextTick(() => {
          //   // that.initLoopCheckTable(that.tableDataClone);
          //   that.initLoopCheckTable(that.tableData);
          // })
          that.initTableDataPermissionStatus(permissionKey,that.tableData);

          that.tableDataClone = that.tableData;
        }else{
          console.info("查询失败");
          loading.close();

          that.tableData = [];
          this.$alert(res.data.msg, '查询失败', {
            confirmButtonText: '确定',
            callback: action => {
              console.info('data:'+JSON.stringify(res.data));
              if(res.data.msg==='请登录认证'){
                this.$router.push({path:'/login'});
              }
            }
          });
        }

      },error=>{
        console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();
        that.tableData = [];
        this.$alert(JSON.stringify(error.message), '查询出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();

        that.tableData = [];
        this.$alert(JSON.stringify(res.message), '查询出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });


    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    submitForm(formName) {
      var that = this;
      console.info("窗体元素："+JSON.stringify( this.permissionForm) );
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.info("valid:"+valid);
         // flag:'',//1是新增，2是修改，3是删除。
          //  layer:'',//1是同级，2是子级


             that.checkPermissionIdSuper(that.permissionForm.oldId,that.tableData,
                {
                  caption:that.permissionForm.caption,
                  id:that.permissionForm.id,
                  vue:that.permissionForm.vue,
                  url:that.permissionForm.url,
                  icon:that.permissionForm.icon,
                  status:"false"
                },
             that.permissionForm.layer,
             that.permissionForm.flag);

             this.refreshTableData = new Date().getTime();

             that.permissionFormVisible1 = false;

             let platfromconfig_detail = '';
             let heatconfig_detail = '';

             if(this.permissionType==='all'){
               if(this.tableData[0].id==='00-0'){
                 console.info("热力用户权限:"+ JSON.stringify( this.tableData[0].detail) );
                 console.info("平台用户权限:"+  JSON.stringify( this.tableData[1].detail));
                 heatconfig_detail = this.tableData[0].detail;
                 platfromconfig_detail = this.tableData[1].detail;
               }else if(this.tableData[0].id==='00-1'){
                 console.info("平台用户权限:"+  JSON.stringify( this.tableData[0].detail));
                 console.info("热力用户权限:"+  JSON.stringify( this.tableData[1].detail));
                 heatconfig_detail = this.tableData[1].detail;
                 platfromconfig_detail = this.tableData[0].detail;
               }
             }else  if(this.permissionType==='heatconfig'){
               console.info("热力用户权限:"+  JSON.stringify( this.tableData));
               heatconfig_detail = this.tableData;
             }else  if(this.permissionType==='platfromconfig'){
               console.info("平台用户权限:"+  JSON.stringify( this.tableData));
               platfromconfig_detail = this.tableData;
             }

          this.postModifyPermisson( JSON.stringify( heatconfig_detail), JSON.stringify( platfromconfig_detail));

        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    /***
     * 增加同级或子级
     * @param curId 当前选中的节点ID
     * @param sourceData tableData数据对象
     * @param targetData 新增加或修改的对象内容
     * @param layer 1是同级，2是子级
     * @param flag 1是新增，2是修改，3是删除,4是查询。5是将status修改成true
     *
     */

    checkPermissionIdSuper(curId,sourceData,targetData,layer,flag){
      console.info("checkPermissionIdSuper:"+sourceData)
      console.info("查询选中ID:"+curId);
      var result = false;
      for(var i1=0;i1<sourceData.length;i1++){
        console.info("当前id:"+sourceData[i1].id);
        if(flag==='1'){
          if(sourceData[i1].id===curId ){
            if(layer==='1'){//同级
              sourceData.push(targetData);
              this.tableDataClone = this.tableData;
              console.info("add find ok");
              result = true;
              break;
            }else if (layer==='2'){
              if(sourceData[i1].hasOwnProperty("detail")){
                console.info("add 存在detail,直接追加:"+JSON.stringify(sourceData[i1]))
                let detail = sourceData[i1].detail;

                detail.push(targetData);
                sourceData[i1].detail = detail;
                this.tableDataClone = this.tableData;
                result = true;
                break;
              }else{
                let detail = [];

                detail.push(targetData);
                sourceData[i1].detail = detail ;
                console.info("add 不存在detail,新创建detail:"+JSON.stringify(sourceData[i1].detail));
                this.tableDataClone = this.tableData;
                result = true;
                break;
              }
            }
            //break;
          } else{
            if(sourceData[i1].hasOwnProperty("detail")){
              console.info("add not find 递归查询detail")
              result = this.checkPermissionIdSuper(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
              console.info("add not find");
              result = false;
            }
          }

        }//end if(flag==='1'){
        else  if(flag==='2'){
          if(sourceData[i1].id===curId ){
            sourceData[i1].caption = targetData.caption;
            sourceData[i1].vue = targetData.vue;
            sourceData[i1].url = targetData.url;
            sourceData[i1].icon = targetData.icon;
            // for(let k of Object.keys(targetData)){
            //   sourceData[i1][k] = targetData[k];
            //   console.info("修改内容：【"+k+"】"+sourceData[i1][k]);
            // }

            console.info("modify find ok");
            this.tableDataClone = this.tableData;
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
              console.info("modify not find 递归查询detail")
              result = this.checkPermissionIdSuper(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
              console.info("modify not find");
              result = false;
            }
          }
        }else if(flag==='3'){
          if(sourceData[i1].id===curId ){
            sourceData.splice(i1, 1);
            this.tableDataClone = this.tableData;
            console.info("del find ok");
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
              console.info("del not find 递归查询detail")
              result = this.checkPermissionIdSuper(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
              console.info("del not find");
              result = false;
            }
          }
        }else if(flag==='4'){
          console.info('查询表格内容');
          //if(sourceData[i1].caption.toLowerCase().includes(curId.toLowerCase())){
          console.info("query find indexOf:"+sourceData[i1].caption.toLowerCase().indexOf(curId.toLowerCase()));
          if(sourceData[i1].caption.toLowerCase().indexOf(curId.toLowerCase())>-1){
            let newdata = [];
            newdata.push(sourceData[i1]);
            this.tableDataClone = newdata;
            console.info("query find ok."+ sourceData[i1].caption);
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
              console.info("query not find 递归查询detail")
              result = this.checkPermissionIdSuper(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
              console.info("query not find");
              this.tableDataClone = [];
              result = false;

            }
          }
        }else if(flag==='5'){
          console.info('查询表格status内容');
          //if(sourceData[i1].caption.toLowerCase().includes(curId.toLowerCase())){
          console.info("query find status id:"+sourceData[i1].id);
          if(sourceData[i1].id===curId){
            sourceData[i1].status = 'true';
            console.info("modify status find ok");
            this.tableDataClone = this.tableData;
            console.info("query find status ok id:"+ sourceData[i1].id);
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
              console.info("query status not find 递归查询detail")
              result = this.checkPermissionIdSuper(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
              console.info("query status not find");
              this.tableDataClone = [];
              result = false;

            }
          }
        }


      }
      return result;

    },

    initLoopCheckTable(rows){//循环检测选中状态
      for (let i = 0; i < rows.length; i++) {



          console.info("initLoopCheckTable_i:"+i+",id:"+rows[i].id+",status:"+rows[i].status);
          if(rows[i].status==='false'){
            // this.tableRowIdMap.set(rows[i].id,false);
            this.tableRowIdMap.delete(rows[i].id );
            this.$refs.multipleTable.toggleRowSelection(rows[i],false)
          }else if(rows[i].status==='true'){
            this.tableRowIdMap.set(rows[i].id,true);

            this.$refs.multipleTable.toggleRowSelection(rows[i],true)
          }
          if(rows[i].detail){
              this.initLoopCheckTable(rows[i].detail);
          }

      }

    },

    handleTreeNodeClick(data, node) {
          console.info('点击事件,可在此处理对应逻辑_data:'+JSON.stringify(data)+",node:"+  node  );
    },
    // handleCheckChange(data, checked, indeterminate) {
    //   console.log("checkChange事件"+data, checked, indeterminate);
    // },
    handleCheck(data,node) {
      console.log("check事件"+ JSON.stringify(data), node);
      console.log("check事件"+ JSON.stringify(data), JSON.stringify( node.checkedKeys) );
      this.treeCheckedKeys = node.checkedKeys;
    },
    showHiddenTree(){

      if(this.showTree){
        this.icon1 = "el-icon-d-arrow-right";
      }else{
        this.icon1 ="el-icon-d-arrow-left";
      }
      this.showTree = !this.showTree;

      console.info("是否显示："+this.showTree);


    },
    tableSelectAll() {//选择全部及不选择
      this.tableCheckedKeys = !this.tableCheckedKeys;

      this.spliteTable(this.tableData, this.tableCheckedKeys);
      let table_arr =  this.$refs.multipleTable.selection;
      console.info("选择的行："+JSON.stringify(table_arr) );

      if(table_arr.length===0){
        this.tableRowIdMap.clear();
      }else{
        for (let i = 0; i < table_arr.length; i++) {
          this.tableRowIdMap.set(table_arr[i].id, true);
        }
      }

    },
    /**
     * 处理数据
     */
    spliteTable(rows, flag) {
      for (let i = 0; i < rows.length; i++) {
        console.info("spliteTable_i:"+i);
        this.$refs.multipleTable.toggleRowSelection(rows[i],flag);
        rows[i].status = flag;

        //this.tableRowIdMap.set(rows[i].id,flag===null?true:false);
        if(this.tableRowIdMap.has(rows[i].id)===false) {
          this.tableRowIdMap.set(rows[i].id, true);
        }else if(this.tableRowIdMap.get(rows[i].id)===true){
          // this.tableRowIdMap.set(rows[i].id,false);
          this.tableRowIdMap.delete(rows[i].id );
        }else {
          this.tableRowIdMap.set(rows[i].id,true);
        }

        if(rows[i].detail){
          this.spliteTable(rows[i].detail,flag);
        }

      }
      console.info("spliteTable_tableRowIdMap:"+JSON.stringify(this.tableRowIdMap));
    },
    //点击当前行checkbox事件 （select事件）
    tableSingleSelect(selection, row){
      if(this.tableRowIdMap.has(row.id)===false) {
        this.tableRowIdMap.set(row.id, true);
      }else if(this.tableRowIdMap.get(row.id)===true){
        // this.tableRowIdMap.set(row.id,false);
        this.tableRowIdMap.delete(row.id );
      }else {
        this.tableRowIdMap.set(row.id,true);
      }
     console.info("tableRowIdMap:"+JSON.stringify(this.tableRowIdMap));
      console.info("tableSingleSelect_row:"+JSON.stringify( row ) );
      if(row.detail){
        this.spliteTable(row.detail);
      }
    },
    //selection-changeg事件，
    handleSelectionChange(selection) {

      this.multipleSelection = selection;
      console.info("multipleSelection选择的行："+JSON.stringify( this.multipleSelection) );
    },
    //选择当前行
    handleSelectCurrent(row){
      console.info("handleSelectCurrent_row.id:"+row.id);
      if(row.status==='false'){
        row.status = 'true';
      }else  if(row.status==='true'){
        row.status = 'false';
      }

      if(row.detail!=undefined){
        this.spliteTable(row.detail);
      }

      this.$refs.multipleTable.toggleRowSelection(row);

      if(this.tableRowIdMap.has(row.id)===false) {
        this.tableRowIdMap.set(row.id, true);
      }else if(this.tableRowIdMap.get(row.id)===true){
        // this.tableRowIdMap.set(row.id,false);
        this.tableRowIdMap.delete(row.id );
      }else {
        this.tableRowIdMap.set(row.id,true);
      }
    },
    isSelect(row, index){//对某列控制是否可以选中或不选中
      // if(row.node){
      //   return false
      // }else{
      //   return true
      // }
      return true;
    },
    handleAddRow(index, row) {
      console.log("行号："+index+",行内容：" + JSON.stringify(row) );
      this.tableDialgTitle = '新增权限点 ['+row.caption+']';
      this.permissionForm.caption = '';
      this.permissionForm.vue = '';
      this.permissionForm.url = '';
      this.permissionForm.icon = '';
      this.permissionForm.oldId = row.id;
      this.permissionForm.id = this.customGlobal.genuuid();
      this.permissionForm.flag = '1';
      this.permissionFormVisible1 = true;
      // this.currentSelTableRow = row;

    },
    handleEditRow(index, row) {
      console.log("行号："+index+",行内容：" + JSON.stringify(row) );
      this.tableDialgTitle = '修改权限点 ['+row.caption+']';
      this.permissionForm.oldId = row.id;
      this.permissionForm.caption = row.caption;
      this.permissionForm.vue = row.vue;
      this.permissionForm.url = row.url;
      this.permissionForm.icon = row.icon;
      this.permissionForm.id = row.id;
      this.permissionForm.flag = '2';
      this.permissionForm.layer ='1';
      this.permissionFormVisible1 = true;
    },
    handleDeleteRow(index, row) {
      console.log("行号："+index+",行内容：" + JSON.stringify(row) );
      this.permissionForm.oldId = row.id;
      this.permissionForm.caption = row.caption;
      this.permissionForm.vue = row.vue;
      this.permissionForm.url = row.url;
      this.permissionForm.icon = row.icon;
      this.permissionForm.id = row.id;
      this.permissionForm.flag = '3';
      this.permissionForm.layer ='1';

      this.$confirm('请再次确认是否要删除[<span style="color:red;">'+row.caption+'</span>]该行记录?', '删除提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString:true,
        closeOnClickModal:false,
        closeOnPressEscape:false,

      }).then(() => {
        this.checkPermissionIdSuper(this.permissionForm.oldId,this.tableData,
          {
            caption:this.permissionForm.caption,
            id:this.permissionForm.id,
            vue:this.permissionForm.vue,
            url:this.permissionForm.url,
            icon:this.permissionForm.icon,
            status:"false"
          },
          this.permissionForm.layer,
          this.permissionForm.flag);

        this.refreshTableData = new Date().getTime();

        this.$message({
          type: 'success',
          message: '删除成功!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    showMessage(){
      let msg = this.customGlobal.mapToJson(this.tableRowIdMap);
      //msg = JSON.stringify( this.customGlobal.jsonToMap(msg));
      this.$message({
        type: 'info',
        showClose: true,
        duration:3000,
        dangerouslyUseHTMLString: true,
        // iconClass:'el-icon-info',
         //message: '<spand style="color:black;"> 已经选择的ID：'+JSON.stringify(this.tableRowIdMap) +"</spand>"
        message: '<spand style="color:black;"> 已经选择的ID：'+ msg +"</spand>"
      });
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, .7)'
      });
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postModifyUserOrgAndPermissionKey,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify({username:'u2',permissionkey:msg})})
      );
      p1.then((res) => {
        console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success'){

          loading.close();
          this.$message({
            type: 'success',
            showClose: true,
            duration:3000,
            dangerouslyUseHTMLString: true,
            // iconClass:'el-icon-info',
            message: '<spand style="color:black;"> 保存成功 </spand>'
          });
        }else{
          console.info("保存失败");
          loading.close();

          this.$alert(res.data.msg, '保存失败', {
            confirmButtonText: '确定',
            callback: action => {
              console.info('data:'+JSON.stringify(res.data));
              if(res.data.msg==='请登录认证'){
                this.$router.push({path:'/login'});
              }
            }
          });
        }

      },error=>{
        console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();
        this.$alert(JSON.stringify(error.message), '保存出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();

        this.$alert(JSON.stringify(res.message), '保存出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });

    },
    showMessage2(){

      this.tableRowIdMap.forEach(function(value,key){
        console.log(key + ': ' + value);
      })



      // this.$message({
      //   type: 'info',
      //   showClose: true,
      //   duration:0,
      //   dangerouslyUseHTMLString: true,
      //   // iconClass:'el-icon-info',
      //   message: '<spand style="color:black;"> 已经选择的KEY：'+  +"</spand>"
      // });
    },

  }
}
</script>

<!--<style scoped>-->
<style  >
.el-tree {
  width: 100%!important;
  overflow: auto!important;
}

.el-tree>.el-tree-node {
  display: inline-block!important;
  min-width: 100%!important;
}

.slide-button  {
  background: #f0f0f0;
  height: calc(100vh);
  width: 25px;
  cursor: pointer;
}
.slide-button:hover {
  background: #dbdbdb;

}
</style>



